@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap");
.menu__side {
  width: 8rem;
  height: 100%;
  background: #3b5895;
  position: fixed;
  top: 8rem;
  left: 0;
  font-size: 1.8rem;
  z-index: 21;
  overflow: hidden;
  overflow-y: scroll;
  border-right: 2rem solid #3b5895;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
  box-shadow: 14px -1px 4px -11px rgba(0, 0, 0, 0.59);
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.menu__side .name__page {
  color: #ffffff;
  padding: 2rem 3rem;
  display: flex;
  align-items: center;
}
.menu__side::-webkit-scrollbar {
  display: none;
}
.menu__side.side__move {
  width: 25rem;
}
@media (max-width: 768px) {
  .menu__side {
    left: -25rem;
    width: 25rem;
  }
  .menu__side.side__move {
    left: 0rem;
  }
}

.barra__progreso {
  padding: 2rem 3rem;
}
.barra__progreso .barra {
  width: 100%;
  height: 0.3rem;
  background: #ffffffb2;
  margin-bottom: 1rem;
  position: relative;
}
.barra__progreso .barra::before {
  content: "";
  position: absolute;
  background: #ffffff;
  width: 75%;
  height: 100%;
  top: 0;
  left: 0;
}
.barra__progreso #progreso__mensaje {
  font-size: 2rem;
  color: #ffffff;
}

.options__menu {
  padding: 2rem 3rem;
  position: absolute;
  top: 18rem;
}
.options__menu a {
  color: #ffffffb2;
  cursor: default;
  display: block;
  position: relative;
  transition: all 0.3s ease;
}
.options__menu a:hover {
  color: #ffffff;
}
.options__menu a.select {
  color: #ffffff;
}
.options__menu a.select::before {
  content: "";
  width: 0.3rem;
  height: 80%;
  position: absolute;
  top: 10%;
  left: -3rem;
  background: #ffffff;
}
.options__menu a .option {
  padding: 2rem 0;
  display: flex;
  align-items: center;
  position: relative;
}
.options__menu a .option i {
  cursor: pointer;
}
.options__menu a .option h4 {
  font-weight: 300;
  cursor: pointer;
}